import { useEffect, useCallback, useState } from "react";
import classes from "./MealsAvailable.module.css";
import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";

const MealsAvailable = () => {
  const [meals, setMeals] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const fetchMealsHandler = useCallback(async () => {
    setIsLoading(true);

    try {
      const response = await fetch(
        "https://react-requests-c4ad1-default-rtdb.europe-west1.firebasedatabase.app/meals.json",
        {}
      );

      if (!response.ok) {
        setIsLoading(false);
        throw new Error("Something went wrong!");
      }

      const data = await response.json();
      const arrayData = [];

      for (const key in data) {
        arrayData.push(data[key]);
      }

      const transformedMeals = arrayData.map((mealData) => {
        return {
          id: mealData.id,
          name: mealData.name,
          price: mealData.price,
          description: mealData.description,
        };
      });

      setMeals(transformedMeals);
      setIsLoading(false);
    } catch (error) {
      setIsLoading(false);
      setError(error.message);
    }
  }, []);

  //Рендер карточек блюд
  const mealsList = meals.map((meal) => (
    <MealItem
      id={meal.id}
      key={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    ></MealItem>
  ));

  useEffect(() => {
    fetchMealsHandler();
  }, [fetchMealsHandler]);

  //Определяем выводимый контент, в зависимости от статуса стейта

  let content = <p>Нет блюд в меню.</p>;

  if (meals.length > 0) {
    content = <ul>{mealsList}</ul>;
  }

  if (error) {
    content = <p>{error}</p>;
  }

  if (isLoading) {
    content = <p>Loading...</p>;
  }

  return (
    <section className={classes.meals}>
      <Card>{content}</Card>
    </section>
  );
};

export default MealsAvailable;
