import { useState, useRef, useEffect } from "react";
import classes from "./Checkout.module.css";
import useInput from "../../hooks/use-input";

const Checkout = (props) => {
  const { onCheckValidity, onInputsReady } = props;

  const {
    value: nameInputValue,
    changeInputHandler: changeNameInputHandler,
    blurInputHandler: blurNameInputHandler,
    hasError: nameInputHasError,
    reset: resetNameInput,
    isValid: nameInputIsValid,
  } = useInput((value) => value.trim() !== "");

  const {
    value: phoneInputValue,
    changeInputHandler: changePhoneInputHandler,
    blurInputHandler: blurPhoneInputHandler,
    hasError: phoneInputHasError,
    reset: resetPhoneInput,
    isValid: phoneInputIsValid,
  } = useInput((value) => value.trim() !== "");

  const {
    value: addressInputValue,
    changeInputHandler: changeAddressInputHandler,
    blurInputHandler: blurAddressInputHandler,
    hasError: addressInputHasError,
    reset: resetAddressInput,
    isValid: addressInputIsValid,
  } = useInput((value) => value.trim() !== "");

  const nameInputRef = useRef();
  const phoneInputRef = useRef();
  const addressInputRef = useRef();

  //Проверка валидности формы и при подтверждении - передача статуса валидности выше в компонент корзины

  useEffect(() => {
    

    if (addressInputIsValid && phoneInputIsValid && nameInputIsValid) {
      
      onCheckValidity(true);
      onInputsReady({
        name: nameInputValue,
        phone: phoneInputValue,
        address: addressInputValue,
      });
    } else {
      
      props.onCheckValidity(false);
    }
  }, [
    addressInputIsValid,
    phoneInputIsValid,
    nameInputIsValid,
    nameInputValue,
    phoneInputValue,
    addressInputValue,
  ]);

  return (
    <form id="checkout-form" className={classes.form}>
      <div className={classes.control}>
        <label htmlFor="name">Ваше имя:</label>
        <input
          type="text"
          id="phone"
          onChange={changeNameInputHandler}
          onBlur={blurNameInputHandler}
          value={nameInputValue}
          ref={nameInputRef}
        />
        {nameInputHasError && <p>Введите имя!</p>}
      </div>
      <div className={classes.control}>
        <label htmlFor="phone">Ваш телефон:</label>
        <input
          type="text"
          id="phone"
          onChange={changePhoneInputHandler}
          onBlur={blurPhoneInputHandler}
          value={phoneInputValue}
          ref={phoneInputRef}
        />
        {phoneInputHasError && <p>Введите телефон!</p>}
      </div>
      <div className={classes.control}>
        <label htmlFor="adress">Адрес доставки:</label>
        <input
          type="text"
          id="adress"
          onChange={changeAddressInputHandler}
          onBlur={blurAddressInputHandler}
          value={addressInputValue}
          ref={addressInputRef}
        />
        {addressInputHasError && <p>Введите адрес!</p>}
      </div>
    </form>
  );
};

export default Checkout;
