import React, { useContext, useState } from "react";
import classes from "./Cart.module.css";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";
import CartContext from "../../store/cart-context";
import Checkout from "./Checkout";

const Cart = (props) => {
  const cartContext = useContext(CartContext);

  const [userData, setUserData] = useState(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [didSubmit, setDidSubmit] = useState(false);
  const [submitError, setSubmitError] = useState(null);

  const totalAmount = `${cartContext.totalAmount.toFixed(2)} руб.`;
  const hasItems = cartContext.items.length > 0;

  let formIsValid = false;

  const cartItemRemoveHandler = (id) => {
    cartContext.removeItem(id);
  };

  const cartItemAddHandler = (item) => {
    cartContext.addItem({ ...item, amount: 1 });
  };

  const submitFormHandler = async () => {
    
    if (userData) {
      setIsSubmitting(true);
      try {
        const response = await fetch(
          "https://react-requests-c4ad1-default-rtdb.europe-west1.firebasedatabase.app/orders.json",
          {
            method: "POST",
            body: JSON.stringify({
              user: userData,
              orderedItems: cartContext.items,
            }),
          }
        );

         setIsSubmitting(false);
         setDidSubmit(true);

        if (!response.ok) {
          throw new Error("Что-то пошло не так :(");
        }
      } catch (error) {
         setDidSubmit(false);
         setIsSubmitting(false);
        setSubmitError(error.message);
      }
    } else {
      return;
    }

    console.log(userData);
  };

  const checkValidity = (status) => {
    formIsValid = status;
  };

  const getInputValues = (userData) => {
    setUserData(userData);
  };

  const cartItems = (
    <ul className={classes["cart-items"]}>
      {cartContext.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );


  const cartModalContent = (
    <React.Fragment>
      {cartItems}
      <div className={classes.total}>
        <span>Всего:</span>
        <span>{totalAmount}</span>
      </div>
      {hasItems && (
        <Checkout
          onCheckValidity={checkValidity}
          onInputsReady={getInputValues}
        />
      )}
      <div className={classes.actions}>
        <button onClick={props.onHideCart} className={classes["button--alt"]}>
          Закрыть
        </button>
        {hasItems && (
          <button
            className={classes.button}
            form={props.form}
            onClick={submitFormHandler}
          >
            Подтвердить
          </button>
        )}
      </div>
    </React.Fragment>
  );

  const isSubmittingModalContent = <p>Отправляю заказ...</p>;

  const didSubmitModalContent = <p>Заказ отправлен :)</p>;

  return (
    <Modal onHideCart={props.onHideCart}>
      {!isSubmitting && !didSubmit && cartModalContent}
      {isSubmitting && isSubmittingModalContent}
      {!isSubmitting && didSubmit && didSubmitModalContent}
     
    </Modal>
  );
};

export default Cart;
